export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './shift.html';\n\nexport default class ExampleBottomBarShift extends Component {\n  static template = _tpl;\n}\n","sass":".demo {\n  :global {\n    .phone-viewport {\n      width: 322px;\n      margin-right: 6px;\n      height: 200px;\n      display: flex;\n      align-items: flex-end;\n      overflow: hidden;\n      border: 1px solid rgb(0 0 0 / 26%);\n      background: rgb(0 0 0 / 6%);\n    }\n  }\n}\n","html":"<!-- import style from './shift.scss' -->\n<div :class=\"style.demo\">\n  <div class=\"phone-viewport\">\n    <md-bottom-bar type=\"shift\">\n      <md-bottom-bar-item to=\"components.bottom-bar\">\n        <md-icon-home class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Home</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item e:to=\"{name: 'components.bottom-bar', params: { sub: 'pages' }}\">\n        <md-icon-pages class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Pages</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item e:to=\"{name: 'components.bottom-bar', params: { sub: 'posts' }}\">\n        <md-icon class=\"md-bottom-bar-icon\" src=\"assets/icon/whatshot.svg\"/>\n        <span class=\"md-bottom-bar-label\">Posts</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item e:to=\"{name: 'components.bottom-bar', params: { sub: 'favorites' }}\">\n        <md-icon-favorite class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Favorites</span>\n      </md-bottom-bar-item>\n    </md-bottom-bar>\n  </div>\n  <div class=\"phone-viewport\">\n    <md-bottom-bar class=\"md-accent\" type=\"shift\">\n      <md-bottom-bar-item>\n        <md-icon-home class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Home</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item>\n        <md-icon-pages class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Pages</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item>\n        <md-icon class=\"md-bottom-bar-icon\" src=\"assets/icon/whatshot.svg\"/>\n        <span class=\"md-bottom-bar-label\">Posts</span>\n      </md-bottom-bar-item>\n      <md-bottom-bar-item>\n        <md-icon-favorite class=\"md-bottom-bar-icon\"/>\n        <span class=\"md-bottom-bar-label\">Favorites</span>\n      </md-bottom-bar-item>\n    </md-bottom-bar>\n  </div>\n</div>"}